html {
  height: 100%;
}
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Layout{
    height: auto;
    /* padding: 35% 5% 35% 5%; */
    padding:0px;
    margin:0px;
}
.Question {
  margin: 0px;
  padding: 30px;
  background-color: #444;
  color: white;
}

.Answer button {
  padding: 18px;
  margin: 3px;
  background-color: #444;
  color: #eee;
  box-shadow: none;
  border: none;
}

.Answers {
  padding: 0px;
  margin: 0px;
  margin-left: auto;
  margin-right: auto;
  position: absolute;
  bottom: 15%;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: center;
          justify-content: center;
}

.Questionbox {
  position: absolute;
  text-align: center;
  top: 20%;
  bottom: 15%;
  left: 8%;
  right: 8%;
  /* background-color: brown; */
  /* transition: all 2s ease-out; */
}

